import { HttpClient } from "@angular/common/http";
import { Injectable } from "@angular/core";
import { AppClientService } from "app/shared/services/app-client.service";

@Injectable({
  providedIn: "root",
})
export class ReportsService extends AppClientService {
  constructor(private http: HttpClient) {
    super(http);
  }

  getAllReportsIndex<T>() {
    return this.get<T>("Admin/get-all-reports-index");
  }

  getReportParameters<T>(reportId: number) {
    return this.get<T>(`Admin/get-report-paramters/${reportId}`);
  }

  getTeachersByCenters<T, TV>(obj: T) {
    return this.post<TV>(`Admin/get-teachers-by-centers`, obj);
  }

  getStudentsByCenters<T, TV>(obj: T) {
    return this.post<TV>("Admin/get-students-by-centers", obj);
  }

  // -----------------------------------------------------
  lecturesExamsViewsCountLectureReport<T, TV>(obj: T) {
    return this.post<TV>("Admin/get-lectures-exams-views-count-lecture", obj);
  }
  lecturesExamsViewsCountExamReport<T, TV>(obj: T) {
    return this.post<TV>(`Admin/get-lectures-exams-views-count-exam`, obj);
  }
  lecturesExamsViewsCountQuizReport<T, TV>(obj: T) {
    return this.post<TV>(`Admin/get-lectures-exams-views-count-quiz`, obj);
  }
  lecturesExamsViewsCountCenterLectureReport<T, TV>(obj: T) {
    return this.post<TV>(
      `Admin/get-lectures-exams-views-count--center-lecture`,
      obj
    );
  }
  lecturesExamsViewsCountLiveLectureReport<T, TV>(obj: T) {
    return this.post<TV>(
      "Admin/get-lectures-exams-views-count-live-lecture",
      obj
    );
  }

  getStudentAbsenceReportExcel(
    selectId: number,
    eduCompId: number,
    typeId: number,
    teacherUserId: number
  ) {
    return this.get(
      `${`Admin/get-student-absence-report-excel`}/${selectId}/${eduCompId}/${typeId}/${teacherUserId}`
    );
  }
  getStudentPresentReportExcel(
    selectId: number,
    eduCompId: number,
    typeId: number,
    teacherUserId: number
  ) {
    return this.get(
      `Admin/get-student-present-report-excel/${selectId}/${eduCompId}/${typeId}/${teacherUserId}`
    );
  }
  getGuestStudentPresentReportExcel(
    selectId: number,
    eduCompId: number,
    typeId: number,
    teacherUserId: number
  ) {
    return this.get(
      `Admin/get-student-guest-present-report-excel/${selectId}/${eduCompId}/${typeId}/${teacherUserId}`
    );
  }

  // -----------------------------------------------------
  examStudentNameReport<T, TV>(obj: T) {
    return this.post<TV>("Admin/get-exam-student-name", obj);
  }
  examStudentReport<T, TV>(obj: T) {
    return this.post<TV>("Admin/get-exam-student-lecture", obj);
  }
  materialStudenReport<T, TV>(obj: T) {
    return this.post<TV>("Admin/get-material-student-lecture", obj);
  }

  // -----------------------------------------------------
  getManualPaymentReportExcel<T, TV>(obj: T) {
    return this.post<TV>("Admin/get-manual-payment-report-excel", obj);
  }

  // -----------------------------------------------------
  getReservationReportExcel<T, TV>(obj: T) {
    return this.post<TV>("Admin/get-reservation-report-excel", obj);
  }

  // -----------------------------------------------------
  getFinancialSummaryReportExcel<T, TV>(obj: T) {
    return this.post<TV>("Admin/get-financial-summary-report-excel", obj);
  }

  // -----------------------------------------------------
  getFinancialDetailsReportExcel<T, TV>(obj: T) {
    return this.post<TV>("Admin/get-financial-details-report-excel", obj);
  }

  // -----------------------------------------------------
  getLecturesSalesReportExcel<T, TV>(obj: T) {
    return this.post<TV>("Admin/get-lecture-sales-report-excel", obj);
  }

  // -----------------------------------------------------
  getFinancialReportExcel<T, TV>(obj: T) {
    return this.post<TV>("Admin/get-financial-report-excel", obj);
  }

  // -----------------------------------------------------
  getMaterialCodesReportExcel<T, TV>(obj: T) {
    return this.post<TV>("Admin/get-material-codes-report-excel", obj);
  }
}
