import { HttpClient } from "@angular/common/http";
import { Injectable } from "@angular/core";
import { AppClientService } from "app/shared/services/app-client.service";

@Injectable({
  providedIn: "root",
})
export class TeacherService extends AppClientService {
  constructor(private http: HttpClient) {
    super(http);
  }

  getTeacherByID(id: any) {
    return this.get("Admin/getTeacherByTeacherUserID/" + id);
  }
}
